import Vue from 'vue'
import Router from 'vue-router'
import axios from 'axios'
import iView from 'iview'
import 'iview/dist/styles/iview.css'

import Admin from 'components/admin/admin'
import Login from 'components/login/login'
import Notfound from 'components/notfound'

Vue.use(Router)
Vue.use(iView)

Vue.prototype.$http = axios

export default new Router({
  // mode: 'history',
  linkActiveClass: 'active',
  routes: [
    {
      path: '/',
      meta: { auth: false },
      redirect: '/admin/index'
    },
    {
      path: '/admin',
      name: 'admin',
      meta: { auth: true },
      component: Admin,
      children: [
        {
          path: 'index',
          meta: { auth: true },
          component: resolve => require(['components/my/my.vue'], resolve)
        },
        {
          path: 'task',
          meta: { auth: true },
          component: resolve => require(['components/task/task.vue'], resolve)
        },
        {
          path: 'bug',
          meta: { auth: true },
          component: resolve => require(['components/bug/bug.vue'], resolve)
        },
        {
          path: 'file',
          meta: { auth: true },
          component: resolve => require(['components/file/file.vue'], resolve)
        },
        {
          path: 'weekly',
          meta: { auth: true },
          component: resolve => require(['components/weekly/weekly.vue'], resolve)
        },
        {
          path: 'system',
          meta: { auth: true },
          component: resolve => require(['components/system/system.vue'], resolve)
        },
        {
          path: 'user',
          meta: { auth: true },
          component: resolve => require(['components/user/user.vue'], resolve)
        }
      ]
    },
    {
      path: '/login',
      name: 'login',
      meta: { auth: false },
      component: Login
    },
    {
      path: '/register',
      name: 'register',
      meta: { auth: false },
      component: Login
    },
    {
      path: '*',
      meta: { auth: false },
      component: Notfound
    }
  ]
})



