<template>
  <div class="layout">
    <h2><img src="../topnav/logo.png">管理系统</h2>
    <Col class="login">
      <Tabs :value="tabs" :animated="false" @on-click="tabClick">
        <Tab-pane label="登陆" icon="person" name="login">
          <Form ref="loginForm" :model="loginForm" :rules="ruleInline">
            <Form-item prop="username">
              <Input type="text" v-model="loginForm.username" placeholder="用户名"></Input>
            </Form-item>
            <Form-item prop="password">
              <Input type="password" v-model="loginForm.password" placeholder="密码"></Input>
            </Form-item>
            <Form-item>
              <Button type="primary" @click="login('loginForm')" style="width:100%;margin:0;">登录</Button>
            </Form-item>
          </Form>
        </Tab-pane>
        <Tab-pane label="注册" icon="person-add" name="register">
          <Form ref="regForm" :model="regForm" :rules="ruleInline">
            <Form-item prop="username">
              <Input type="text" v-model="regForm.username" placeholder="用户名"></Input>
            </Form-item>
            <Form-item prop="email">
              <Input type="text" v-model="regForm.email" placeholder="邮箱"></Input>
            </Form-item>
            <Form-item prop="password">
              <Input type="password" v-model="regForm.password" placeholder="密码"></Input>
            </Form-item>
            <Form-item prop="passtrue">
              <Input type="password" v-model="regForm.passtrue" placeholder="重复密码"></Input>
            </Form-item>
            <Form-item>
              <Button type="primary" @click="register('regForm')">注册</Button>
              <Button type="ghost" @click="reset('regForm')">重置</Button>
            </Form-item>
          </Form>

        </Tab-pane>
      </Tabs>
    </Col>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data () {
      const validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else if (value.length < 6){
          callback(new Error('密码长度不能小于6位'));
        } else {
          if (this.regForm.passtrue !== '') {
            this.$refs.regForm.validateField('passtrue');
          }
          callback();
        }
      };
      const validatePassCheck = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value.length < 6){
          callback(new Error('密码长度不能小于6位'));
        } else if (value !== this.regForm.password) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      return {
        tabs: this.$route.name,
        loginForm: {
          username: '',
          password: ''
        },
        regForm: {
          username: '',
          email: '',
          password: '',
          passtrue: ''
        },
        ruleInline: {
          username: [
            {required: true, message: '请填写用户名', trigger: 'blur'}
          ],
          email: [
            {required: true, message: '请填写邮箱', trigger: 'blur'},
            {type: 'email', message: '邮箱格式不正确', trigger: 'blur'}
          ],
          password: [
            { validator: validatePass, trigger: 'blur'}
          ],
          passtrue: [
            { validator: validatePassCheck, trigger: 'blur'}
          ]
        }
      }
    },
    mounted () {
    },
    methods: {
      login (name) {
        this.$refs[name].validate((valid) => {
          let that = this
          if (valid) {
            that.$Loading.start();
            that.$http.post('/api/login',this.loginForm)
              .then(function (res) {
                that.$Message.success('登陆成功!')
                that.$Loading.finish()
              })
              .catch(function (err) {
                that.$Message.error('登陆失败!')
                that.$Loading.error()
              })
          } else {
            that.$Message.error('表单验证失败!')
          }
        })
      },
      register (name) {
        this.$refs[name].validate((valid) => {
          let that = this
          if (valid) {
            that.$Loading.start()
            that.$http.post('/api/register',this.regForm)
              .then(function (res) {
                that.$Message.success('注册成功!')
                that.$Loading.finish()
              })
              .catch(function (err) {
                that.$Message.error('注册失败!')
                that.$Loading.error()
              })
          } else {
            that.$Message.error('表单验证失败!')
          }
        })
      },
      reset (name) {
        this.$refs[name].resetFields();
      },
      tabClick (name) {
        this.tabs = name
        this.$router.push('/'+ name)
        console.log(this);
        for(let name in this.$refs){
          this.$refs[name].resetFields();
        }
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .layout
    width: 250px
    margin: 10% auto
    h2
      text-align: center
      margin-bottom: 10px
      font-size: 20px
      img
        width: 80px
        height: 80px
        display: block
        margin: 5px auto
  .login
    width: 100%
  .login > .ivu-tabs > .ivu-tabs-bar
    border: 0
  .login .ivu-tabs-nav
    display: block
    float: none
    text-align: center
  .login > .ivu-tabs
    .ivu-tabs-bar .ivu-tabs-ink-bar
      visibility: hidden
    .ivu-tabs-bar .ivu-tabs-tab-active
      border-bottom: 3px solid #39f
  .ivu-form .ivu-form-item .ivu-form-item-content
    text-align: center
    button
      width: 40%
      margin: 0 5px

</style>


